'use client';

import { useState } from 'react';
import AnimatedBackground from './AnimatedBackground';
import HackerGridBackground from './HackerGridBackground';
import CircuitBackground from './CircuitBackground';

type BackgroundType = 'matrix' | 'grid' | 'circuit';

const BackgroundSwitcher = () => {
  const [backgroundType] = useState<BackgroundType>('matrix'); // Default to matrix

  const renderBackground = () => {
    switch (backgroundType) {
      case 'matrix':
        return <AnimatedBackground />;
      case 'grid':
        return <HackerGridBackground />;
      case 'circuit':
        return <CircuitBackground />;
      default:
        return <AnimatedBackground />;
    }
  };

  return renderBackground();
};

export default BackgroundSwitcher;
