'use client';

import { useEffect, useRef } from 'react';

const HackerGridBackground = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Set canvas size
    const resizeCanvas = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    resizeCanvas();
    window.addEventListener('resize', resizeCanvas);

    // Grid properties
    const gridSize = 50;
    let time = 0;

    // Binary code rain
    const binaryChars = ['0', '1'];
    const columns = Math.floor(canvas.width / 10);
    const drops: number[] = [];

    for (let i = 0; i < columns; i++) {
      drops[i] = Math.random() * -100;
    }

    const draw = () => {
      // Clear canvas with dark background
      ctx.fillStyle = 'rgba(0, 0, 0, 0.1)';
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      // Draw animated grid
      ctx.strokeStyle = 'rgba(0, 255, 0, 0.1)';
      ctx.lineWidth = 1;

      // Vertical lines
      for (let x = 0; x < canvas.width; x += gridSize) {
        const offset = Math.sin(time * 0.001 + x * 0.01) * 5;
        ctx.beginPath();
        ctx.moveTo(x + offset, 0);
        ctx.lineTo(x + offset, canvas.height);
        ctx.stroke();
      }

      // Horizontal lines
      for (let y = 0; y < canvas.height; y += gridSize) {
        const offset = Math.cos(time * 0.001 + y * 0.01) * 5;
        ctx.beginPath();
        ctx.moveTo(0, y + offset);
        ctx.lineTo(canvas.width, y + offset);
        ctx.stroke();
      }

      // Binary rain
      ctx.fillStyle = 'rgba(0, 255, 0, 0.8)';
      ctx.font = '12px monospace';

      for (let i = 0; i < drops.length; i++) {
        const char = binaryChars[Math.floor(Math.random() * 2)];
        ctx.fillText(char, i * 10, drops[i]);

        if (drops[i] > canvas.height && Math.random() > 0.975) {
          drops[i] = 0;
        }
        drops[i] += 0.5;
      }

      // Glitch effect lines
      if (Math.random() > 0.98) {
        const y = Math.random() * canvas.height;
        const height = 2 + Math.random() * 3;
        ctx.fillStyle = 'rgba(255, 0, 0, 0.3)';
        ctx.fillRect(0, y, canvas.width, height);
      }

      time++;
    };

    const interval = setInterval(draw, 16); // ~60fps

    return () => {
      clearInterval(interval);
      window.removeEventListener('resize', resizeCanvas);
    };
  }, []);

  return (
    <canvas
      ref={canvasRef}
      className="fixed top-0 left-0 w-full h-full pointer-events-none z-[-1]"
      style={{ 
        background: 'radial-gradient(circle at 50% 50%, rgba(0, 40, 0, 0.4) 0%, rgba(0, 0, 0, 0.9) 100%)',
        opacity: 0.4
      }}
    />
  );
};

export default HackerGridBackground;
